import 'react-native';
import React from 'react';
import sort from '../sortReservations';

// fetch is not found globally and no fetcher passed, to fix pass a fetch for test environment.
global.fetch = require('node-fetch');

// Note: test renderer must be required after react-native.
import renderer from 'react-test-renderer';

it('sorts reservations correctly', () => {
  const unsortedData = [
    { hotelName: 'Hilton', arrivalDate: '12/30/2019'},
    { hotelName: 'Hilton', arrivalDate: '12/28/2019'},
    { hotelName: 'Hilton', arrivalDate: '04/30/2019'},
    { hotelName: 'Hilton', arrivalDate: '1/30/2020'},
    { hotelName: 'Marriot', arrivalDate: '11/21/2019'},
    { hotelName: 'Marriot', arrivalDate: '10/30/2019'},    
  ];
  const sortedData = [
    [
      { hotelName: 'Hilton', arrivalDate: '04/30/2019'},
      { hotelName: 'Hilton', arrivalDate: '12/28/2019'},
      { hotelName: 'Hilton', arrivalDate: '12/30/2019'},
      { hotelName: 'Hilton', arrivalDate: '1/30/2020'},
    ],
    [
      { hotelName: 'Marriot', arrivalDate: '10/30/2019'},  
      { hotelName: 'Marriot', arrivalDate: '11/21/2019'},
    ]
  ];
  sort(unsortedData, returnResult);
  const returnResult = (sortedReservations) => {
    expect(JSON.stringify(sortedReservations)).toEqual(JSON.stringify(sortedData));

  }
});
